
.fooddetail{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 51px;
    background-color: #fff;
}
.fooddetail-img,
.fooddetail-img img{
    width: 100%;
    height: 414px;
}
.fooddetail-img{
    position: relative;
    /* background-color: orange; */
}
.fooddetail-img .btn{
    position: absolute;
    width: 30px;
    height: 30px;
    background-color: #ccc;
    color: #fff;
    text-align: center;
    border-radius: 50%;
    font-size: 26px;
    line-height: 26px;
    top: 10px;
    right: 10px;
}
.fooddetail-info{
    width: 100%;
    height: 120px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0 15px;
    /* background-color: red; */
    position: relative;
}
.fooddetail-info .cartcontrol-wrapper{
    position: absolute;
    top: 84px;
    right: 15px;
}
.fooddetail-info .fooddetail-name{
    font-size: 24px;
    padding: 20px 0 15px 0;
}
.fooddetail-info .fooddetail-good{
    font-size: 13px;
    color: #666;
    margin-bottom: 15px;
}
.fooddetail-info .fooddetail-price{
    color:red;
    font-size: 18px;
}



